<template>
  <div>
    <fTitle>
      <template #title>Select 选择器</template>
      <template #introduce>当选项过多时，使用下拉菜单展示并选择内容。</template>
    </fTitle>
    <sTitle>
      <template #title>各种基础用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
         <h3>基础用法</h3>   <el-select v-model="value1" placeholder="请选择">
        <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
         </el-select>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>单选项禁用</h3> <el-select v-model="value2" placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value"
      :disabled="item.disabled">
    </el-option>
  </el-select>
          </div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
           <h3>整个禁用</h3>
      <el-select v-model="value3" disabled placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select></div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>可清空单选</h3>
      <el-select v-model="value4" clearable placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select></div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h3>多选不重叠</h3> <el-select v-model="value5" multiple placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select> </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>多选重叠</h3><el-select v-model="value6" multiple collapse-tags style="margin-left: 20px;" placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select></div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h3>自定义排版</h3>  <el-select v-model="value7" placeholder="请选择">
    <el-option
      v-for="item in cities"
      :key="item.value"
      :label="item.label"
      :value="item.value">
      <span style="float: left">{{ item.label }}</span>
      <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
    </el-option>
  </el-select>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>选项分组</h3>
           <el-select v-model="value8" placeholder="请选择">
    <el-option-group
      v-for="group in options2"
      :key="group.label"
      :label="group.label">
      <el-option
        v-for="item in group.options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-option-group>
  </el-select>
          </div></el-col>
        </el-row>
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
            <h3>搜索匹配</h3>  <el-select v-model="value9" filterable placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>任意选项匹配</h3> <el-select
    v-model="value10"
    multiple
    filterable
    allow-create
    default-first-option
    placeholder="请选择文章标签">
    <!-- default-first-option搜索框内匹配的第一个选项回车直接选择 -->
    <!-- allow-create是指输入不存在的选项可以生成新的选项啊 -->
    <el-option
      v-for="item in options3"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
          </div></el-col>
        </el-row>
      </div></div>
  </div>

</template>

<script>
