      }, {
        value: 'JavaScript',
        label: 'JavaScript'
      }],
      value1: '',
      value2: '',
      value3: '',
      value4: '',
      value5: [],
      value6: [],
      value7: '',
      value8: '',
      value9: '',
      value10: ''
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        }
    }}
</style>
